/* Navigation.css */
@import "../assets/colors/colors.css";
.navigation {
    background-color: var(--color-white); /* Màu xanh lá */
    padding: 20px;
    display: flex; /* Sử dụng flexbox thay thay float */
    align-items: center; /* Canh giữa theo chiều dọc */
    justify-content: space-between; /* Canh đều hai đầu */
    width: auto;

  }
  
  .navigation ul {
    list-style-type: none;
    display: flex;
  }
  
  .navigation li {
    padding-left: 30px;
    padding-right: 30px;
  }
  
  .navigation a {
    text-decoration: none;
    color: var(--color-black);; /* Màu chữ trắng */
    font-weight: bold;
  }
  .navigation img {
    float: left;
  }